<template>
  <div>
    <analysis-header @refresh="refresh" @interval="interval"></analysis-header>
    <a-divider></a-divider>
    <div id="base">
      <a-row>
        <a-col :span="12" class="col">
          <div class="chart" id="bar"></div>
        </a-col>
        <a-col :span="12" class="col">
          <div class="chart" id="nested-pie"></div>
        </a-col>
      </a-row>
      <a-row class="word-cloud">
        <a-col :span="5" class="dropdown-col">
          <city-dropdown @onClick="onClick"></city-dropdown>
        </a-col>
        <a-col :span="19" class="word-cloud-col">
          <div class="chart" id="word-cloud"></div>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import {
  getAvgRentBar,
  getTagsCloud,
  getTypeNestedPie
} from "network/analysis";
import AnalysisHeader from "./childComp/AnalysisHeader";
import CityDropdown from "./childComp/CityDropdown";

export default {
  name: "Bar",
  data() {
    return {
      avgRentBar: "",
      tagsWordCloud: "",
      nestedPie: "",
      isInterval: false
    };
  },
  components: {
    AnalysisHeader,
    CityDropdown
  },
  mounted() {
    this.avgRentBar = echarts.init(document.getElementById("bar"), "white", {
      renderer: "canvas"
    });
    this.tagsWordCloud = echarts.init(
      document.getElementById("word-cloud"),
      "white",
      {
        renderer: "canvas"
      }
    );
    this.nestedPie = echarts.init(
      document.getElementById("nested-pie"),
      "white",
      {
        renderer: "canvas"
      }
    );
    this.$nextTick(() => {
      this.getEchart();
    });
  },
  methods: {
    /**
     * 获取图表
     */
    getEchart() {
      getAvgRentBar().then(res => {
        this.avgRentBar.setOption(res.data.data);
      });
      getTagsCloud().then(res => {
        this.tagsWordCloud.setOption(res.data.data);
      });
      getTypeNestedPie().then(res => {
        this.nestedPie.setOption(res.data.data);
      });
    },
    chartInterval() {
      if (!this.isInterval) {
        return;
      }
      this.getEchart();
      setTimeout(this.chartInterval, 5000);
    },
    /**
     * 刷新与定时器
     */
    refresh() {
      this.getEchart();
    },
    interval(isInterval) {
      this.isInterval = isInterval;
      this.chartInterval();
    },
    onClick(key) {
      getTagsCloud(key).then(res => {
        this.tagsWordCloud.setOption(res.data.data);
      });
    }
  }
};
</script>

<style scoped>
#base {
  height: 600px;
  overflow: hidden;
  overflow-y: scroll;
}
.chart {
  left: 15%;
  width: 50vh;
  height: 50vh;
}
#nested-pie {
  left: 0;
  width: 100%;
  height: 60vh;
  margin-top: 0px;
}
.dropdown-col {
  text-align: center;
}
.word-cloud-col {
  padding: 0 !important;
}
.word-cloud{
  margin-top: 10px;
}
</style>